	module.exports = {"data":{"markdownRemark":{"html":"<h2>jQuery Effects Show Method</h2>\n<p>In its simplest form, <strong>.show()</strong> displays the matched element immediately, with no animation. For example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token function\">$</span><span class=\"token punctuation\">(</span><span class=\"token string\">\".myclass\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">show</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>will show all the elements whose class is <em>myclass</em>. Any jQuery selector can be used.</p>\n<p>However, this method does not override <code>!important</code> in the CSS style, such as <code>display: none !important</code>. </p>\n<h3>.show() as an animation method</h3>\n<p>Thanks to its options, <strong>.show()</strong> can animate the width, height, and opacity of the matched elements simultaneously. </p>\n<ul>\n<li>\n<p>Duration can be provided in milliseconds, or using the literals slow (600 ms) and fast(200ms). for example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token function\">$</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"#myobject\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">show</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"slow\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n</li>\n<li>\n<p>A function can be specified to be called once the animation is complete, once per every matched element. for example</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token function\">$</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"#title\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">show</span><span class=\"token punctuation\">(</span> <span class=\"token string\">\"slow\"</span><span class=\"token punctuation\">,</span> <span class=\"token keyword\">function</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n<span class=\"token function\">$</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"p\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">show</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"fast\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n</li>\n<li>\n<p>More options exist, please refer to the official website for further details. </p>\n</li>\n</ul>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p>JQuery Show() method on the <a href='http://api.jquery.com/show/' target='_blank' rel='nofollow'>Official website</a> </p>","fields":{"slug":"/jquery/jquery-effects-show-method/"},"frontmatter":{"title":"jQuery Effects Show Method"}}},"pathContext":{"slug":"/jquery/jquery-effects-show-method/"}}

